.mintgui {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  padding: 10% 0% 10% 0%;
  margin: 0% 0% 0% 0%;
  overflow: hidden;
}

.mintguiLeftContainer {
  flex: 50%;
  display: flex;
  justify-content: flex-end;
  padding: 0% 0% 0% 10%;
}

.mintguiRightContainer {
  flex: 50%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.mintguiImage {
  border-radius: 50px;
  width: 80%;
  align-items: left;
  justify-content: left;
  float: left;
}


.mintguiTextContainer {
  width: 100%;
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 0px 0px;
  display: flex;
  align-items: left;
  justify-content: center;
  flex-direction: column;
}

.mintguiText {
  text-align: left;
  color: white;
  font-size: 16px;
}

.mintguiTitle {
  font-size: 23px;
  text-decoration: underline;
}

.mintguiLink {
  width: 45%;
}

.mintguiLink:hover {
  -webkit-transform: scale(1.10);
  -moz-transform: scale(1.10);
  -o-transform: scale(1.10);
  transform: scale(1.10);
}

.mintguiLink:active {
  -webkit-transform: scale(1.15);
  -moz-transform: scale(1.15);
  -o-transform: scale(1.15);
  transform: scale(1.15);
}

@media only screen and (max-width: 560px) {
  .mintguiBoxIMG {
    width: 100%;
  }
  .mintgui {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 100vw;
    margin: 0% 0% 0% 0%;
    padding: 15% 0% 20% 0%;
  }

  .mintguiRight {
    align-items: center;
    justify-content: center;
  }


  .mintguiLeftContainer {
    padding: 0% 5% 0% 5%;
    margin: 0% 0% 0% 0%;
  }

  .mintguiTextContainer {
    padding: 0% 0% 0% 0%;
    margin: 0% 0% 0% 0%;
  }

  .mintguiTitle {
    font-size: 20.5px;
  }

  .mintguiTagline {
    font-size: 14px;
  }

  .mintguiImage {
    width: 90%;
    padding: 0% 0% 0% 5%;
    margin: 10% 0% 0% 0%;
  }

  .mintguiLink {
    width: 100%;
  }

}
